.banner {
  width: 100%;
  height: 223px;
  border-radius: 25px;
  position: relative;
  overflow: hidden;
  box-shadow: 0px 4px 4px 0px #00000040;
  .bg-banner {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    z-index: 2;
    opacity: 0.6;

    &.about {
      opacity: 0.3;
    }
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include xy-0;
    z-index: 1;
  }

  p {
    @include xy-center;
    font-weight: 700;
    font-size: 48px;
    color: $secondary-color;
    z-index: 3;
  }
}

@media (max-width: 600px) {
  .banner {
    height: 111px;
    box-shadow: unset;
    border-radius: 10px;
    p {
      font-size: 24px;
      bottom: 50%;
      left: 16px;
      top: unset;
      transform: translateY(50%);
      max-width: 68%;
    }
  }
}
